:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

.gallery__item {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.list__item {
  margin-top: 30px;
  margin-bottom: 20px;
  margin-left: 30px;
}

.btn {
  max-width: 100px;
  text-shadow: 1px 1px #fff;
  font-size: 12px;
  font: inherit;
  color: #4f5055;
  cursor: pointer;
  background-color: #ebecf0;
  border: 0;
  border-radius: 20px;
  outline: 0;
  margin-bottom: 50px;
  padding: 8px 12px;
  font-weight: bold;
  text-decoration: none;
  transition: all .2s ease-in-out;
  box-shadow: inset 3px 3px 8px #f5f110, inset -3px -3px 8px #077ff7;
}

.btn:hover {
  box-shadow: -2px -2px 5px #fff, 2px 2px 5px #6f9dee;
}

.btn:active {
  box-shadow: inset 1px 1px 2px #babecc, inset -1px -1px 2px #fff;
}

.goback-btn {
  cursor: pointer;
  width: 44px;
  height: 44px;
  color: #4f5055;
  transform: all 250ms linear;
  background: #f3fdf8;
  border: none;
  border-radius: 20px;
  margin-left: 30px;
  padding: 8px 12px;
  box-shadow: inset 3px 3px 8px #f7f30b, inset -3px -3px 8px #3a9af3;
}

